/* Two-column layout for NBSTUP Membership Account page */
.pmpro-nbstup-account-layout {
  display: flex;
  gap: 32px;
  align-items: flex-start;
  margin: 20px 0;
  padding: 16px;
  border-radius: 8px;
  background: #f8fafc;
}

.pmpro-nbstup-account-sidebar {
  flex: 0 0 280px;
  max-width: 280px;
  padding: 24px;
  position: sticky;
  top: 20px;
  background: #ffffff;
  border: 1px solid #e1e5e9;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav li + li {
  margin-top: 4px;
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a {
  display: block;
  padding: 12px 16px;
  border-radius: 6px;
  color: #374151;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.2s ease;
  border: 1px solid transparent;
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a:hover, .pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a:focus {
  background-color: #f3f4f6;
  color: #111827;
  border-color: #d1d5db;
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a.active {
  background-color: #3b82f6;
  color: #ffffff;
  border-color: #3b82f6;
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a.active:hover {
  background-color: #2563eb;
  border-color: #2563eb;
}

.pmpro-nbstup-account-content {
  flex: 1 1 auto;
  min-width: 0;
  padding: 24px;
  background: #ffffff;
  border: 1px solid #e1e5e9;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}
.pmpro-nbstup-account-content h2,
.pmpro-nbstup-account-content h3 {
  color: #111827;
  margin-top: 24px;
  margin-bottom: 16px;
}
.pmpro-nbstup-account-content h2:first-child,
.pmpro-nbstup-account-content h3:first-child {
  margin-top: 0;
}
.pmpro-nbstup-account-content h2 {
  font-size: 1.5rem;
  font-weight: 700;
}
.pmpro-nbstup-account-content h3 {
  font-size: 1.125rem;
  font-weight: 600;
}
.pmpro-nbstup-account-content p {
  color: #374151;
  line-height: 1.6;
  margin-bottom: 12px;
}
.pmpro-nbstup-account-content table {
  width: 100%;
  border-collapse: collapse;
  background: #ffffff;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid #e5e7eb;
}
.pmpro-nbstup-account-content table th,
.pmpro-nbstup-account-content table td {
  padding: 12px 16px;
  border-bottom: 1px solid #e5e7eb;
  text-align: left;
  vertical-align: top;
}
.pmpro-nbstup-account-content table th {
  background: #f9fafb;
  color: #374151;
  font-weight: 600;
}
.pmpro-nbstup-account-content table tr:last-child td {
  border-bottom: none;
}
.pmpro-nbstup-account-content .pmpro_btn,
.pmpro-nbstup-account-content .button {
  padding: 8px 16px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 500;
  display: inline-block;
  transition: all 0.2s ease;
  cursor: pointer;
  border: 1px solid transparent;
  font-size: inherit;
  line-height: inherit;
}
.pmpro-nbstup-account-content .pmpro_btn:active, .pmpro-nbstup-account-content .pmpro_btn:focus,
.pmpro-nbstup-account-content .button:active,
.pmpro-nbstup-account-content .button:focus {
  outline: 2px solid rgba(59, 130, 246, 0.5);
  outline-offset: 2px;
}
.pmpro-nbstup-account-content .pmpro_btn,
.pmpro-nbstup-account-content .button {
  background-color: #3b82f6;
  color: #ffffff;
  border-color: #3b82f6;
}
.pmpro-nbstup-account-content .pmpro_btn:hover,
.pmpro-nbstup-account-content .button:hover {
  background-color: #2563eb;
  border-color: #2563eb;
}

/* Deceased Members Table Styles */
.pmpro-nbstup-deceased-table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
  font-size: 0.95rem;
}
.pmpro-nbstup-deceased-table th,
.pmpro-nbstup-deceased-table td {
  padding: 12px 16px;
  text-align: left;
  border-bottom: 1px solid #e5e7eb;
}
.pmpro-nbstup-deceased-table th {
  background-color: #f9fafb;
  font-weight: 600;
  color: #374151;
  border-bottom: 2px solid #d1d5db;
  text-transform: uppercase;
  font-size: 0.85rem;
  letter-spacing: 0.5px;
}
.pmpro-nbstup-deceased-table tbody tr:hover {
  background-color: #f9fafb;
}
.pmpro-nbstup-deceased-table tbody tr:hover .button {
  opacity: 1;
}
.pmpro-nbstup-deceased-table tbody tr:last-child td {
  border-bottom: 1px solid #d1d5db;
}
.pmpro-nbstup-deceased-table .member-avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
}
.pmpro-nbstup-deceased-table .button {
  padding: 8px 16px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 500;
  display: inline-block;
  transition: all 0.2s ease;
  cursor: pointer;
  border: 1px solid transparent;
  font-size: inherit;
  line-height: inherit;
}
.pmpro-nbstup-deceased-table .button:active, .pmpro-nbstup-deceased-table .button:focus {
  outline: 2px solid rgba(59, 130, 246, 0.5);
  outline-offset: 2px;
}
.pmpro-nbstup-deceased-table .button {
  background-color: #3b82f6;
  color: #ffffff;
  border-color: #3b82f6;
}
.pmpro-nbstup-deceased-table .button:hover {
  background-color: #2563eb;
  border-color: #2563eb;
}
.pmpro-nbstup-deceased-table .button {
  opacity: 0.9;
  transition: all 0.3s ease;
}

.pmpro-nbstup-users-list {
  max-width: 100%;
  overflow-x: auto;
}

.pmpro-nbstup-search-form {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
  padding: 12px 14px;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  background: #ffffff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.04);
  margin-bottom: 20px;
}
.pmpro-nbstup-search-form input[type=text] {
  flex: 1 1 260px;
  padding: 10px 12px;
  min-width: 220px;
  border: 1px solid #d0d5dd;
  border-radius: 8px;
  outline: none;
  transition: border-color 0.15s ease, box-shadow 0.15s ease;
  background: #fff;
}
.pmpro-nbstup-search-form input[type=text]:focus {
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.15);
}
.pmpro-nbstup-search-form button,
.pmpro-nbstup-search-form .button {
  padding: 5px 15px;
  border-radius: 8px;
  border: 1px solid #2563eb;
  background: linear-gradient(135deg, #2563eb, #3b82f6);
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  font-weight: 600;
  line-height: 2;
  transition: background 0.15s ease, border-color 0.15s ease, transform 0.05s ease;
}
.pmpro-nbstup-search-form .button {
  background: #ffffff;
  color: #111827;
  border-color: #d0d5dd;
}
.pmpro-nbstup-search-form button:hover {
  background: linear-gradient(135deg, #1d4ed8, #2563eb);
  border-color: #1d4ed8;
}
.pmpro-nbstup-search-form .button:hover {
  background: #f3f4f6;
}
.pmpro-nbstup-search-form button:active,
.pmpro-nbstup-search-form .button:active {
  transform: translateY(1px);
}

@media (max-width: 600px) {
  .pmpro-nbstup-search-form {
    padding: 10px;
  }
  .pmpro-nbstup-search-form input[type=text] {
    flex-basis: 100%;
  }
  .pmpro-nbstup-search-form button,
  .pmpro-nbstup-search-form .button {
    width: 100%;
    text-align: center;
  }
}
.pmpro-nbstup-users-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  background: #ffffff;
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.04);
}
.pmpro-nbstup-users-table th,
.pmpro-nbstup-users-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}
.pmpro-nbstup-users-table th {
  background-color: #f5f5f5;
  font-weight: bold;
  color: #374151;
}
.pmpro-nbstup-users-table tr:nth-child(even) {
  background-color: #f9f9f9;
}
.pmpro-nbstup-users-table tr:hover {
  background-color: #f0f0f0;
}

.pmpro-nbstup-pagination {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.pmpro-nbstup-pagination a,
.pmpro-nbstup-pagination span {
  padding: 8px 12px;
  border: 1px solid #ddd;
  text-decoration: none;
  border-radius: 8px;
}
.pmpro-nbstup-pagination a:hover {
  background-color: #f0f0f0;
}
.pmpro-nbstup-pagination .current {
  background-color: #0073aa;
  color: white;
  border-color: #0073aa;
}

.pmpro-nbstup-pagination-summary {
  text-align: center;
  margin-top: 10px;
}

.status-badge {
  display: inline-block;
  padding: 3px 8px;
  border-radius: 3px;
  font-size: 11px;
  font-weight: bold;
}

.status-active,
.status-yes {
  background-color: #d4edda;
  color: #155724;
}

.status-inactive,
.status-no {
  background-color: #f8d7da;
  color: #721c24;
}

.pmpro-nbstup-contrib-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  margin: 20px 0;
}

.pmpro-nbstup-contrib-stat {
  background: #fff;
  border-left: 4px solid #2271b1;
  padding: 15px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
  border-radius: 8px;
}
.pmpro-nbstup-contrib-stat.is-danger {
  border-left-color: #d63638;
}
.pmpro-nbstup-contrib-stat.is-success {
  border-left-color: #00a32a;
}

.pmpro-nbstup-contrib-stat-value {
  font-size: 32px;
  font-weight: bold;
  color: #2271b1;
}
.pmpro-nbstup-contrib-stat-value.is-danger {
  color: #d63638;
}
.pmpro-nbstup-contrib-stat-value.is-success {
  color: #00a32a;
}

.pmpro-nbstup-contrib-stat-label {
  color: #646970;
}

.pmpro-nbstup-contrib-filters {
  background: #fff;
  padding: 15px;
  margin: 20px 0;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.04);
}

.pmpro-nbstup-contrib-filters-row {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  align-items: center;
}

.pmpro-nbstup-contrib-filter-select {
  min-width: 150px;
}

.pmpro-nbstup-contrib-filter-search {
  min-width: 250px;
}

.pmpro-nbstup-status-icon {
  margin-right: 4px;
}

.pmpro-nbstup-status-paid {
  color: #00a32a;
}

.pmpro-nbstup-status-unpaid {
  color: #d63638;
}

.pmpro-nbstup-status-empty {
  color: #646970;
}

.pmpro-nbstup-contrib-quick-actions {
  margin-top: 20px;
  padding: 15px;
  background: #fff;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.04);
}

.pmpro-nbstup-bullet-list {
  list-style: disc;
  margin-left: 20px;
}

.pmpro-nbstup-shortcodes-box {
  background: #f0f0f1;
  padding: 15px;
  margin: 20px 0;
  border-left: 4px solid #2271b1;
}

.pmpro-nbstup-shortcodes-title {
  margin-top: 0;
}

.pmpro-nbstup-shortcodes-table {
  background: #fff;
  margin-top: 10px;
}

.pmpro-nbstup-shortcodes-cell {
  padding: 10px;
}

.pmpro-nbstup-shortcodes-row-alt {
  background: #f9f9f9;
}

.pmpro-nbstup-shortcodes-features-title {
  margin-top: 15px;
}

.pmpro-nbstup-section-divider {
  margin: 40px 0;
}

.pmpro-nbstup-tab-content {
  margin-top: 20px;
}

.pmpro-nbstup-admin-split {
  display: flex;
  gap: 30px;
  align-items: flex-start;
  flex-wrap: wrap;
}

.pmpro-nbstup-admin-col-fixed {
  flex: 0 0 300px;
}

.pmpro-nbstup-admin-col-fluid {
  flex: 1;
}

.pmpro-nbstup-member-login {
  max-width: 520px;
  margin: 40px auto;
  padding: 36px;
  background: linear-gradient(180deg, #ffffff 0%, #f9fbff 100%);
  border: 1px solid #e5e7eb;
  border-radius: 18px;
  box-shadow: 0 12px 30px rgba(17, 24, 39, 0.08);
  position: relative;
  overflow: hidden;
}
.pmpro-nbstup-member-login::before {
  content: "";
  position: absolute;
  inset: 0 0 auto 0;
  height: 5px;
  background: linear-gradient(90deg, #3b82f6, #dbeafe, #93c5fd);
}

.pmpro-nbstup-member-login__message {
  margin-bottom: 18px;
  padding: 12px 14px;
  border-radius: 12px;
  border: 1px solid rgba(239, 68, 68, 0.2);
  background: rgba(239, 68, 68, 0.06);
  color: #374151;
  font-weight: 600;
  border-left: 4px solid #ef4444;
}

.pmpro-nbstup-member-login__form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.pmpro-nbstup-member-login__header {
  text-align: center;
  margin-bottom: 8px;
}

.pmpro-nbstup-member-login__title {
  margin: 0 0 6px;
  font-size: 1.6rem;
  font-weight: 700;
  color: #111827;
}

.pmpro-nbstup-member-login__subtitle {
  margin: 0;
  color: #4b5563;
  font-size: 0.95rem;
}

.pmpro-nbstup-member-login__field {
  margin-bottom: 0;
}

.pmpro-nbstup-member-login__label {
  display: block;
  margin-bottom: 6px;
  font-weight: 600;
  color: #374151;
  letter-spacing: 0.2px;
}

.pmpro-nbstup-member-login__input {
  width: 100%;
  padding: 12px 14px;
  border: 1px solid #e1e5e9;
  border-radius: 12px;
  background: #ffffff;
  transition: border-color 0.15s ease, box-shadow 0.15s ease, background 0.15s ease;
  color: #111827;
}
.pmpro-nbstup-member-login__input:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.15);
  outline: none;
}
.pmpro-nbstup-member-login__input::placeholder {
  color: #d1d5db;
}

.pmpro-nbstup-member-login__field.has-error .pmpro-nbstup-member-login__input {
  border-color: #ef4444;
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.15);
}

.pmpro-nbstup-field-error {
  margin-top: 8px;
  font-size: 0.85rem;
  color: #ef4444;
}

.pmpro-nbstup-member-login__actions {
  display: flex;
  justify-content: center;
  margin-top: 4px;
  gap: 8px;
}

.pmpro-nbstup-member-login__submit {
  min-width: 200px;
  border-radius: 999px;
  font-weight: 700;
  letter-spacing: 0.3px;
  box-shadow: 0 10px 20px rgba(37, 99, 235, 0.18);
}

.pmpro-nbstup-login-tabs__header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 24px;
  position: relative;
}

.pmpro-nbstup-login-tab {
  flex: 1 1 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
  min-height: 46px;
  border: none;
  background: transparent;
  color: #4b5563;
  padding: 12px 16px;
  border-radius: 12px;
  font-weight: 600;
  letter-spacing: 0.2px;
  cursor: pointer;
  transition: all 0.2s ease;
}
.pmpro-nbstup-login-tab:hover {
  color: #111827;
  background: rgba(255, 255, 255, 0.7);
}
.pmpro-nbstup-login-tab:focus-visible {
  outline: 2px solid rgba(59, 130, 246, 0.5);
  outline-offset: 2px;
}
.pmpro-nbstup-login-tab.is-active {
  background: #ffffff;
  color: #111827;
  box-shadow: 0 10px 24px rgba(17, 24, 39, 0.12);
}

@media (max-width: 480px) {
  .pmpro-nbstup-login-tabs__header {
    flex-direction: column;
    align-items: stretch;
    border-radius: 8px;
    padding: 4px;
  }
  .pmpro-nbstup-login-tab {
    width: 100%;
    border-radius: 8px;
  }
}
.pmpro-nbstup-login-panel[hidden] {
  display: none;
}

.pmpro-nbstup-login-panel {
  padding-top: 4px;
}

.pmpro-nbstup-login-message {
  margin-bottom: 16px;
  padding: 12px 14px;
  border-radius: 12px;
  border: 1px solid rgba(239, 68, 68, 0.2);
  background: rgba(239, 68, 68, 0.06);
  color: #374151;
  border-left: 4px solid #ef4444;
}

.pmpro-nbstup-login__checkbox label {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #374151;
  font-weight: 500;
}
.pmpro-nbstup-login__checkbox input {
  margin: 0;
  width: 16px;
  height: 16px;
  accent-color: #3b82f6;
}

.pmpro-nbstup-login-form.is-loading {
  opacity: 0.75;
  pointer-events: none;
}

.pmpro-nbstup-login-form.is-loading .pmpro-nbstup-member-login__submit {
  opacity: 0.85;
  cursor: progress;
}

/* Pagination Styles */
.pmpro-nbstup-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-top: 24px;
  flex-wrap: wrap;
  justify-content: center;
}
.pmpro-nbstup-pagination .button {
  padding: 8px 16px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 500;
  display: inline-block;
  transition: all 0.2s ease;
  cursor: pointer;
  border: 1px solid transparent;
  font-size: inherit;
  line-height: inherit;
}
.pmpro-nbstup-pagination .button:active, .pmpro-nbstup-pagination .button:focus {
  outline: 2px solid rgba(59, 130, 246, 0.5);
  outline-offset: 2px;
}
.pmpro-nbstup-pagination .button {
  background-color: #6b7280;
  color: #ffffff;
  border-color: #6b7280;
}
.pmpro-nbstup-pagination .button:hover {
  background-color: #4b5563;
  border-color: #4b5563;
}
.pmpro-nbstup-pagination span {
  color: #6b7280;
  font-weight: 500;
}
.pmpro-nbstup-pagination .button:disabled,
.pmpro-nbstup-pagination .button[aria-disabled=true] {
  opacity: 0.5;
  cursor: not-allowed;
}
.pmpro-nbstup-pagination .button:disabled:hover,
.pmpro-nbstup-pagination .button[aria-disabled=true]:hover {
  background-color: #6b7280;
  border-color: #6b7280;
}

.pmpro-nbstup-badge {
  display: inline-block;
  padding: 4px 12px;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.pmpro-nbstup-badge.badge-success {
  background-color: rgba(16, 185, 129, 0.1);
  color: #10b981;
}
.pmpro-nbstup-badge.badge-warning {
  background-color: rgba(245, 158, 11, 0.1);
  color: #f59e0b;
}
.pmpro-nbstup-badge.badge-danger {
  background-color: rgba(239, 68, 68, 0.1);
  color: #ef4444;
}
.pmpro-nbstup-badge.badge-info {
  background-color: rgba(59, 130, 246, 0.1);
  color: #3b82f6;
}

/* Tablet Breakpoint (1024px) */
@media (max-width: 1024px) {
  .pmpro-nbstup-account-layout {
    gap: 24px;
  }
  .pmpro-nbstup-account-sidebar {
    flex: 0 0 240px;
    max-width: 240px;
    padding: 20px;
  }
}
/* Mobile Breakpoint (768px) */
@media (max-width: 768px) {
  .pmpro-nbstup-account-layout {
    flex-direction: column;
    gap: 20px;
    margin: 12px 0;
  }
  .pmpro-nbstup-account-sidebar {
    flex: 0 0 auto;
    max-width: 100%;
    position: static;
    padding: 16px;
  }
  .pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a {
    padding: 12px 12px;
  }
  .pmpro-nbstup-account-content {
    padding: 16px;
  }
  .pmpro-nbstup-account-content h2 {
    font-size: 1.25rem;
  }
  .pmpro-nbstup-account-content h3 {
    font-size: 1rem;
  }
  .pmpro-nbstup-deceased-table {
    margin: 16px 0;
  }
